.modal-container.is-close {
  display: none;
}

.modal-container {
  position: fixed;
  top: 0;
  left: 0;
  // .header имеет position fixed, z-index 10.
  z-index: 11;

  display: flex;

  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.8);
}

.modal {
  position: relative;

  margin: 146px auto;
  width: 500px;
  height: 610px;
  padding: 38px 40px 40px;
  border-radius: 3px;

  background-color: #00B3FF;
}

.modal-callback {}

.modal__close-button {
  position: absolute;
  top: 15px;
  right: 15px;

  width: 40px;
  height: 40px;
  border: none;

  background-color: inherit;

  &::before,
  &::after {
    content: "";

    position: absolute;
    top: 50%;

    width: 28px;
    height: 1px;
    border-radius: 5px;

    background-color: #ffffff;
  }

  &::before {
    transform: translateX(-50%) rotate(45deg);
  }

  &::after {
    transform: translateX(-50%) rotate(-45deg);
  }
}


.modal-content {}

.modal__title {
  margin: 0;
  margin-bottom: 6px;

  font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
  font-size: 30px;
  line-height: 41px;
  font-weight: 400;
  color: #ffffff;
}

.modal__text {
  margin: 0;
  margin-bottom: 36px;

  font-family: "PT Mono", Arial, Helvetica, sans-serif;
  font-size: 16px;
  line-height: 21px;
  font-weight: 400;
  color: #ffffff;
}




